import { ATTR } from './AttribLocations';

export enum DRAW_MODE {
    // 
}

export class RTN {
    name: string;
    
    vao: WebGLVertexArrayObject | null;
    bufVertices: WebGLBuffer | null;
    bufNormals: WebGLBuffer | null;
    bufUV: WebGLBuffer | null;
    bufIndex: WebGLBuffer | null;

    vertexComponentLen: number;
    vertexCount: number;
    indexCount: number;
    drawMode: number;

    constructor() {
        // 
    }
}

export class VAO {
    name: string;

    noCulling: boolean = true;
    doBlending: boolean = true;
    
    vao: WebGLVertexArrayObject | null;
    bufVertices: WebGLBuffer | null;
    bufNormals: WebGLBuffer | null;
    bufUV: WebGLBuffer | null;
    bufIndex: WebGLBuffer | null;

    vertexComponentLen: number;
    vertexCount: number;
    indexCount: number;
    drawMode: number;

    constructor(gl: WebGL2RenderingContext, name: string , aryVert: number[], aryInd?: number[] | null, aryNorm?: number[] | null, aryUV?: number[] | null ) {
        this.name = name;
        this.drawMode = gl.TRIANGLES;

        this.vao = gl.createVertexArray();															
        gl.bindVertexArray(this.vao);	//  Bind it so all the calls to vertexAttribPointer/enableVertexAttribArray is saved to the vao.

        // .......................................................
        // Set up vertices
        if (aryVert !== undefined && aryVert != null) {
            this.bufVertices = gl.createBuffer();													// Create buffer...
            this.vertexComponentLen = 3;																// How many floats make up a vertex
            this.vertexCount = aryVert.length / this.vertexComponentLen;								// How many vertices in the array

            gl.bindBuffer(gl.ARRAY_BUFFER, this.bufVertices);
            gl.bufferData(gl.ARRAY_BUFFER, new Float32Array(aryVert), gl.STATIC_DRAW);		// then push array into it.
            gl.enableVertexAttribArray(ATTR.ATTR_POSITION_LOC);										// Enable Attribute location
            gl.vertexAttribPointer(ATTR.ATTR_POSITION_LOC, 3, gl.FLOAT, false, 0, 0);						// Put buffer at location of the vao
        }

        // .......................................................
        // Setup normals
        if (aryNorm !== undefined && aryNorm != null) {
            this.bufNormals = gl.createBuffer();
            gl.bindBuffer(gl.ARRAY_BUFFER, this.bufNormals);
            gl.bufferData(gl.ARRAY_BUFFER, new Float32Array(aryNorm), gl.STATIC_DRAW);
            gl.enableVertexAttribArray(ATTR.ATTR_NORMAL_LOC);
            gl.vertexAttribPointer(ATTR.ATTR_NORMAL_LOC, 3, gl.FLOAT, false, 0, 0);
        }

        // .......................................................
        // Setup UV
        if (aryUV !== undefined && aryUV != null) {
            this.bufUV = gl.createBuffer();
            gl.bindBuffer(gl.ARRAY_BUFFER, this.bufUV);
            gl.bufferData(gl.ARRAY_BUFFER, new Float32Array(aryUV), gl.STATIC_DRAW);
            gl.enableVertexAttribArray(ATTR.ATTR_UV_LOC);
            gl.vertexAttribPointer(ATTR.ATTR_UV_LOC, 2, gl.FLOAT, false, 0, 0);	// UV only has two floats per component
        }

        // .......................................................
        // Setup Index.
        if (aryInd !== undefined && aryInd != null) {
            this.bufIndex = gl.createBuffer();
            this.indexCount = aryInd.length;
            console.log(this);
            gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, this.bufIndex);
            gl.bufferData(gl.ELEMENT_ARRAY_BUFFER, new Uint16Array(aryInd), gl.STATIC_DRAW);
            // gl.bindBuffer(gl.ELEMENT_ARRAY_BUFFER, null);

        }

        // Clean up
        gl.bindVertexArray(null);					// Unbind the VAO, very Important. always unbind when your done using one.
        gl.bindBuffer(gl.ARRAY_BUFFER, null);	// Unbind any buffers that might be set

        // this.mMeshCache[name] = rtn;
    }

}